import React, { useState, useEffect } from 'react'
import { GoogleLogin } from 'react-google-login'
import { Card, CardBody, Button, Row, Col } from 'reactstrap'
import { gapi } from "gapi-script"
import axios from '@src/configs/axios/axiosConfig'
import { orgUserId } from '@src/helper/sassHelper'
import toast from 'react-hot-toast'
const userId = orgUserId()
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
const MySwal = withReactContent(Swal)
import GDriveImage from '@src/assets/images/icons/drive.png'
import GmailImage from '@src/assets/images/icons/gmail.png'
const ConnectionsTabContent = () => {

  const SCOPE = 'https://www.googleapis.com/auth/drive https://www.googleapis.com/auth/drive.file https://www.googleapis.com/auth/gmail.send'

  const [Emailprofile, setEmailProfile] = useState({})
  const [activeTab, setActiveTab] = useState(2)
  const [Driveprofile, setDriveProfile] = useState({})
  const [logindata, setLOginData] = useState({})
  const [connectionScope, setConnectionScope] = useState(SCOPE)
  const clientId = process.env.REACT_APP_GOOGLE_CLIENT_ID

  useEffect(() => {
    gapi.load('client:auth2', () => {
      gapi.client.init({
        clientId,
        scope: connectionScope
      })
    })
  }, [connectionScope])

  const onSuccess = (res, tab) => {
    setActiveTab(tab)
    if (res?.code !== undefined) {
      setLOginData(res)
    }
  }

  const getLogin = () => {

    axios.post('/google/getlogin')
      .then((res) => {
        const email = res.data.data.find((obj) => { return parseInt(obj.type) === 1 })
        const drive = res.data.data.find((obj) => { return parseInt(obj.type) === 2 })
        setEmailProfile(email || {})
        setDriveProfile(drive || {})
      })
      .catch((err) => { console.log(err) })

  }


  const onSuccessLogout = (type) => {

    const auth2 = gapi.auth2.getAuthInstance()
    auth2.signOut().then(function () {
      axios.post('/google/logout', { type })
        .then(() => {
          MySwal.fire({
            icon: 'success',
            title: 'Logout!',
            text: 'Successfully Logout.',
            customClass: {
              confirmButton: 'btn btn-success'
            }
          })
          getLogin()
        })
        .catch((err) => { console.log(err) })
    })
  }

  useEffect(() => {
    getLogin()
  }, [])

  const googleLogin = () => {

    axios.post('/google/googleredirect', { type: activeTab, code: logindata.code, createdBy: userId })
      .then(() => {
        getLogin()
        setLOginData({})
        toast.success('Email Linked Successfully', { position: 'bottom-right' })
      })
      .catch((err) => { console.log(err) })
  }

  useEffect(() => {
    if (Object.keys(logindata).length > 0) {
      googleLogin()
    }
  }, [logindata])

  useEffect(() => {
    getLogin()
  }, [])

  const onFailure = (err) => {
    console.log('failed', err)
  }

  const logoutFn = (type) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, Logout!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        onSuccessLogout(type)
      }
    })
  }

  useEffect(() => {
    setConnectionScope(SCOPE)
  }, [])

  return (
    <Card>
      <CardBody>
        <h5>Apps</h5>
        <hr></hr>
        <Row >
          <Col className='d-flex align-items-center'>
            <img className='rounded border p-md-50' height={35} width={35} src={GmailImage}
              alt="G Mail" />
          </Col>
          <Col>
            <p className='p-md-25 m-0'>GMail</p>
            <p className='p-md-25 m-0'>by<span className='ms-md-50 text-primary'>Google</span></p>
            {Object.keys(Emailprofile).length > 0 &&
              <div>
                <p className='p-md-25 m-0'>Status : <span className='text-success'>Connected</span></p>
                <p className='p-md-25 m-0'> Email : {Emailprofile.email}<span></span></p>
              </div>
            }
          </Col>
          <Col>
            <h6 className='m-0'>Features</h6>
            <p className='m-0'> Send Automated Mail to client through GMail </p>
          </Col>
          <Col className='d-flex align-items-center justify-content-end'>
            {Object.keys(Emailprofile).length === 0 &&
              <GoogleLogin
                clientId={clientId}
                scope={connectionScope}
                prompt='consent'
                theme='dark'
                redirectUri="postmessage"
                accessType="offline"
                responseType="code"
                onSuccess={(res) => onSuccess(res, 1)}
                onFailure={onFailure}
                isSignedIn={true}
              />
            }
            {Object.keys(Emailprofile).length > 0 && <Button color='danger' onClick={() => logoutFn(1)}>Revoke</Button>}
          </Col>
        </Row>
        <hr></hr>
        <Row>
          <Col className='d-flex align-items-center'>
            <img className='rounded border  p-md-50' height={35} width={35} src={GDriveImage}
              alt="Google Drive" />
          </Col>
          <Col>
            <p className='p-md-25 m-0'>Google Drive</p>
            <p className='p-md-25 m-0'>by<span className='ms-md-50 text-primary'>Google</span></p>
            {Object.keys(Driveprofile).length > 0 &&
              <div>
                <p className='p-md-25 m-0'>Status : <span className='text-success'>Connected</span></p>
                <p className='p-md-25 m-0'> Email : {Driveprofile.email}<span></span></p>
              </div>
            }
          </Col>
          <Col>
            <h6 className='m-0'>Features</h6>
            <p className='m-0'> Save Files in Google Drive Storage </p>
          </Col>
          <Col className='d-flex align-items-center justify-content-end'>
            {Object.keys(Driveprofile).length === 0 &&
              <GoogleLogin
                clientId={clientId}
                scope={connectionScope}
                prompt='consent'
                redirectUri="postmessage"
                accessType="offline"
                responseType="code"
                theme='dark'
                onSuccess={(res) => onSuccess(res, 2)}
                onFailure={onFailure}
                isSignedIn={true}
              />
            }
            {Object.keys(Driveprofile).length > 0 &&
              <Button color='danger' onClick={() => logoutFn(2)}>Revoke</Button>
            }
          </Col>
        </Row>
        <hr></hr>
        <p className='text-primary' style={{ fontStyle: 'italic' }}>Disclaimer</p>

        <p className='font-small-4' style={{ fontStyle: 'italic' }}>Turia does not send emails to your clients or save your files in our server. Turia lets you connect your Gmail to send emails and Google Drive for file storage.</p>
        <p className='font-small-4' style={{ fontStyle: 'italic' }}>Turiaâ€™s does not share or transfer information received from Google APIs to any other app. The app adheres to
          <a target={'_blank'} href='https://developers.google.com/terms/api-services-user-data-policy'> Google API Services User Data Policy</a>, including the Limited Use requirements.</p>
      </CardBody>
    </Card>
  )
}
export default ConnectionsTabContent